.hamburger {
    display: none;
    position: absolute;
    right: 1px;
    top: 26px;
    width: 36px;
    height: 36px;
    cursor: pointer;
}

@media (max-width: 1024px) {
    .hamburger {
        display: block;
    }
}

.hamburger-line, .hamburger-line:before, .hamburger-line:after{
    width: 36px;
    position: absolute;
    height: 5px;
    background: #000000;
    display: block;
    transition: .3s;
    border-radius: 5px;
}

.hamburger-line:before{
    content: "";
    top: 8px;
    transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.hamburger-line {
    top: 20px;
}

.hamburger-line:after{
    content: "";
    bottom: 8px;
    transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.hamburger-close {
    display: none;
    position: absolute;
    right: 1px;
    top: 26px;
    width: 36px;
    height: 36px;
    cursor: pointer;
}

.hamburger-close_line {
    position: absolute;
    right: 1px;
    top: 5px;
    width: 32px;
    height: 32px;
}

.hamburger-close_line:before, .hamburger-close_line:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #000000;
}

.hamburger-close_line:before {
    transform: rotate(45deg);
}

.hamburger-close_line:after {
    transform: rotate(-45deg);
}


